body {
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 800px;
  background-color: #fff;
  border-radius: 5px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

h1 {
  text-align: center;
}

.search-form {
  text-align: center;
  margin-bottom: 20px;
}

.search-form input {
  width: 70%;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  transition: border-color .3s;
}

.search-form input:focus {
  border-color: #007bff;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  transition: background-color .3s;
}

.search-form button:hover {
  background-color: #0056b3;
}

.gallery {
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  display: grid;
}

.photo-card {
  text-align: center;
  cursor: pointer;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
}

.photo-card img {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  transition: background-color .3s;
  display: block;
}

.load-more:hover {
  background-color: #0056b3;
}

.modal {
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .7);
  padding-top: 50px;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal img {
  max-width: 80%;
  max-height: 80%;
  margin: 0 auto;
  display: block;
}

.close-button {
  cursor: pointer;
  color: #fff;
  font-size: 30px;
  position: absolute;
  top: 10px;
  right: 10px;
}

